.main-categories-cards-main-container {
  display: flex;
  flex-direction: row;
}

.main-categories-cards-main-container-row-1 {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding-top: 4%;
  padding-bottom: 4%;
  padding-left: 100px;
  gap: 50px;
  background-color: #e5e5e5;
}

.main-categories-cards-main-container-row-1-card-1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 25px;
  width: 80%;
  padding: 3vh 2vw;
  gap: 0vh;
  align-self: flex-start;
  position: relative;
  background-color: white;
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.4);
  cursor: pointer;
  transition: all 0.5s ease-in;
  text-decoration: none;
  color: black;
}

.main-categories-cards-main-container-row-1-card-1-icon{
  width: 10%;
}

.main-categories-cards-main-container-row-1-card-1 h1 {
  text-align: center;
  color: #000;
  font-size: 2vw;
}

.main-categories-cards-main-container-row-1-card-1:hover {
  background-color: #612d91;
  cursor: pointer;
}

.main-categories-cards-main-container-row-1-card-1:hover h1 {
  color: #fff;
}

.main-categories-cards-main-container-row-1-card-1:hover p {
  color: #fff;
}

.main-categories-cards-main-container-row-1-card-1 p {
  text-align: center;
  line-height: 2;
  font-size: 1vw;
  z-index: 5;
  color: #000;
}

.arrow-1 {
  position: absolute;
  top: 27.5vh;
  /* top: 125%; */
  left: -6vw; /* Adjust the value according to your preference */
  transform: rotate(6deg);
  /* transform: translateY(-50%); */
  height: 27.5vw;
}

.triangle-1 {
  position: absolute;
  top: 2.5vh;
  left: -5vw; /* Adjust the value according to your preference */
  transform: translateY(-50%);
  width: 3vw;
  cursor: default;
}

